@media screen and (max-width: 767px) {
  .header {
    background: #fff;
    justify-content: space-between;
    padding: 25px 40px;
    display: flex;
  }

  .header-container {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    display: flex;
  }

  .logo-container {
    margin-left: 20px;
  }

  .logo-header, .logo-p, .address {
    display: none;
  }

  .address-list {
    margin: 10px;
    padding: 25px;
    display: block;
  }

  .address-item {
    letter-spacing: .05em;
    color: var(--black);
    padding-top: 35px;
    font-family: Nunito, sans-serif;
    font-size: 25px;
    font-weight: 400;
  }

  .menu-container {
    width: 100%;
    height: 100%;
    background-color: var(--light);
    min-height: 80px;
    transition: transform .25s ease-in-out;
    display: inline-flex;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(100%);
    box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
  }

  .menu-container.is-open {
    transform: translateX(0);
  }

  .menu-toggle {
    background-color: var(--white-color);
    cursor: pointer;
    border: none;
    outline: none;
    justify-content: flex-end;
    align-items: center;
    margin: 0;
    padding: 0;
    display: flex;
  }

  .header-menu {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .modal-menu-svg {
    width: 24px;
    height: 24px;
    fill: var(--light-black);
    background-color: var(--dark-red);
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    padding: 0;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 24px;
    right: 24px;
  }

  .modal-menu-svg:hover, .modal-menu-svg:focus {
    background-color: var(--light-brown);
    fill: var(--white-color);
    border: none;
  }

  .modal-svg {
    transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  }

  .mobile-menu {
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    list-style: none;
    display: inline-flex;
  }
}

